<script>
import { EmailAuthCode } from "@/components";
import SwapLeg from "@/components/SwapLeg";

export default {
  name: "user-profile",
  data() {
    return {
      loading: false,
      model: {
        username: "",
        email: "",
        name: "",
        last_name: "",
        address: "",
        city: "",
        country: "",
        zip: "",
        about: "",
        link: "",
        btc_wallet: "",
      },
    };
  },
  components: { EmailAuthCode, SwapLeg },
  created() {
    this.model = this.$store.getters.currentUser;
  },
  methods: {
    changePicture(env) {
      env.preventDefault();
      this.loading = true;
      let data = new FormData();
      data.append("file", this.$refs.picture.files[0]);

      this.$http({
        method: "put",
        url: "/api/v1/client/users/" + this.$store.getters.currentUser.uuid,
        data: data,
        headers: {
          "content-type": "application/json",
          Authorization: this.$store.getters.authToken,
        },
      })
        .then(() => {
          this.$store.dispatch("getUser");
          this.$toasted.show("Profile picture updated!", {
            position: "top-right",
            duration: 5000,
            type: "success",
            closeOnSwipe: true,
          });
          this.loading = false;
        })
        .catch((error) => {
          error.response.data &&
            error.response.data.map((m) => {
              this.$toasted.show(m, {
                position: "top-right",
                duration: 5000,
                type: "error",
                closeOnSwipe: true,
              });
            });
          this.loading = false;
        });
    },
    onCopy() {
      this.$toasted.show("Copied to clippboard!", {
        position: "top-right",
        duration: 5000,
        type: "success",
        closeOnSwipe: true,
      });
    },
    swapLeg(leg) {
      leg = leg === "left" ? false : true;
      this.loading = true;
      this.updateUser({ right: leg });
    },
    updateUser() {
      this.loading = true;
      this.$http({
        method: "put",
        url: "/api/v1/client/users/" + this.$store.getters.currentUser.uuid,
        data: this.model,
        headers: {
          "content-type": "application/json",
          Authorization: this.$store.getters.authToken,
        },
      })
        .then(() => {
          this.$store.dispatch("getUser");
          this.$toasted.show("Profile Updated!", {
            position: "top-right",
            duration: 5000,
            type: "success",
            closeOnSwipe: true,
          });
          this.loading = false;
        })
        .catch((error) => {
          error.response.data &&
            error.response.data.map((m) => {
              this.$toasted.show(m, {
                position: "top-right",
                duration: 5000,
                type: "error",
                closeOnSwipe: true,
              });
            });
          this.loading = false;
        });
    },
  },
};
</script>
