import Player from "./Player";
import Control from "./Control";
import Countdown from "@/components/Countdown.vue";

export default {
  name: "ad-view",
  data() {
    return {
      playerEnabled: true
    };
  },
  components: {
    Player,
    Control,
    Countdown
  },
  created() {
    this.$store.dispatch("adView/initPendingTasks");
  },
  methods: {
    nextAd() {
      this.playerEnabled = false;
      setTimeout(() => {
        this.playerEnabled = true;
      }, 1000);
    }
  }
};
