<template>
  <div>
    <CircularTimer
      class="timer"
      :initial-value="scuntilnoon"
      :stroke-width="5"
      :hours-stroke-color="'#000'"
      :underneath-stroke-color="'lightgrey'"
      :hours-fill-color="'transparent'"
      :size="243"
      :padding="5"
      :show-negatives="true"
      :notify-every="'minute'"
    ></CircularTimer>
    <!-- <h3 class="text-center">
      Until Daily Cash-Out
    </h3> -->
  </div>
</template>
<script>
import CircularTimer from "./CircularTimer"

export default {
  name: "countdown",
  components:{
    CircularTimer
  },
  created() {
    this.noon();
  },
  data(){
    return {
      scuntilnoon: 0
    }
  },
  methods: {
    noon() {
      var miamitime = this.calcTime(-5);
      var noon = new Date();
      noon.setHours(22,0,0,0);
      var diff = noon - miamitime;
      if(diff<0){
        diff+=86400000;
      }
      this.scuntilnoon= diff/1000;
    },
    calcTime(offset) {
      var d = new Date();
      var utc = d.getTime() + d.getTimezoneOffset() * 60000;
      var nd = new Date(utc + 3600000 * offset);
      return nd;
    }
  }
};
</script>
