                  title="Remove"
                >
                  <i class="fa fa-times"></i>
                </button>
              </div>
            </div>
            <div class="box-body">
              <div class="table-responsive">
                <table class="table table-bordered no-margin">
                  <thead class="bg-dark">
                    <tr>
                      <th class="text-white text-left">ID</th>
                      <th class="text-white text-left">Hash (TXID)</th>
                      <th class="text-white text-left">Price</th>
                      <th class="text-white text-left">Pack</th>
                      <th class="text-white text-center">Status</th>
                      <th class="text-white text-right">Date</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="invoice in tableData" :key="invoice.id">
                      <template v-if="invoice.invoice_status === 'paid'">
                        <td class="text-left">{{ invoice.id }}</td>
                        <td class="text-left">
                          <a href="#" class="text-yellow hover-warning">{{
                            invoice.txid
                          }}</a>
                        </td>
