                          {{ invoice.price }} {{ invoice.currency.name }}
                        </td>

                        <td class="text-left">{{ invoice.plan.name }}</td>
                        <td class="text-center">
                          <span
                            class="label"
                            :class="
                              (invoice.invoice_status === 'pending' &&
                                'label-warning') ||
