                              (invoice.invoice_status === "cancelled" &&
                                "Canceled")
                            }}
                          </span>
                        </td>
                        <td class="text-right">
                          <time>{{
                            invoice.payment_confirmed_at
                              | formatDate("MMMM Do YYYY hh:mm A")
                          }}</time>
                        </td>
                      </template>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="row">
        <div class="col-12">
          <div class="box box-solid bg-black">
            <div class="box-header with-border">
              <h3 class="box-title">Unconfirmed Invoices</h3>

              <div class="box-tools pull-right">
                <button
                  type="button"
                  class="btn btn-box-tool"
