                        <td class="text-center">
                          <span
                            class="label"
                            :class="
                              (invoice.invoice_status === 'pending' &&
                                'label-warning') ||
                              (invoice.invoice_status === 'paid' &&
                                'label-success') ||
                              (invoice.invoice_status === 'cancelled' &&
                                'label-danger')
                            "
                          >
                            {{
                              (invoice.invoice_status === "pending" &&
                                "Unconfirmed") ||
                              (invoice.invoice_status === "paid" &&
                                "Confirmed") ||
                              (invoice.invoice_status === "cancelled" &&
                                "Canceled")
                            }}
                          </span>
                        </td>
                        <td class="text-right">
                          <router-link
                            :to="'/invoices/' + invoice.id"
                            class="text-yellow hover-warning"
                            >Details</router-link
                          >
                        </td>
                        <td class="text-right">
                          <time>
                            {{
                              invoice.created_at
                                | formatDate("MMMM Do YYYY hh:mm A")
