</template>

<script>
import Vue from "vue";
import VueClipboard from "vue-clipboard2";
import VueQrcode from "@chenfengyuan/vue-qrcode";

import { formatDate, formatDollar } from "@/filters";

Vue.use(VueClipboard);
Vue.component(VueQrcode.name, VueQrcode);

export default {
  data() {
    return {
      type: "",
      loading: false,
      tableData: [],
      modal: false,
      invoice: {},
    };
  },
  filters: { formatDate, formatDollar },
  created() {
    this.getData();
  },
  methods: {
    onCopy() {
      this.$notify({
        type: "danger",
        title: "Copied !",
      });
    },

    getData() {
      this.loading = true;
      this.$http
        .get("/api/v1/client/invoices", {
          headers: {
            "content-type": "application/json",
            Authorization: this.$store.getters.authToken,
          },
        })
        .then((response) => {
          this.tableData = response.data;
          this.loading = false;
        })
        .catch((error) => {
          error.response.data &&
            error.response.data.map((m) => {
              this.$toasted.show(m, {
                position: "top-right",
                duration: 5000,
                type: "error",
                closeOnSwipe: true,
              });
            });
          this.loading = false;
        });
    },

    setInvoice(object) {
      this.modal = true;
      this.invoice = object;
    },

    close() {
      this.modal = false;
    },
  },
};
</script>
