      <div class="navbar-custom-translate translate-menu d-none d-sm-flex">
        <GoogleTranslate id="goog-translate" />
      </div>

      <div class="navbar-custom-invite">
        <form class="d-none d-sm-flex" v-if="$store.getters.currentUser.active">
          <div class="container">
            <div class="input-group">
              <span class="input-group-addon"
                ><i class="icon-plane text-white"></i
              ></span>
              <input
                type="text"
                class="form-control"
                :value="
                  this.domain +
                    '/#/register/lineBy/' +
                    $store.getters.currentUser.username +
                    '/branch/' +
