<script>
import GoogleTranslate from "@/components/GoogleTranslate.vue";
import { userMixin } from "@/mixins";

export default {
  name: "top-menu",
  components: {
    GoogleTranslate
  },
  data() {
    return {
      model: {},
      domain: ""
    };
  },
  mixins: [userMixin],
  methods: {
    swapLeg(leg) {
      leg = leg === "left" ? false : true;
      this.loading = true;
      this.$http({
        method: "put",
        url: "/api/v1/client/users/" + this.$store.getters.currentUser.uuid,
        data: { right: leg },
        headers: {
          "content-type": "application/json",
          Authorization: this.$store.getters.authToken
        }
      })
        .then((response) => {
          this.$store.commit("setCurrentUser", response.data);
          this.loading = false;
          this.$toasted.show("Information updated!", {
            position: "top-right",
            duration: 5000,
            type: "success",
            closeOnSwipe: true
          });
        })
        .catch((error) => {
          error.response.data &&
            error.response.data.map((m) => {
              this.$toasted.show(m, {
                position: "top-right",
                duration: 5000,
                type: "error",
                closeOnSwipe: true
              });
            });
          this.loading = false;
        });
    },
    onCopy() {
      this.$toasted.show("Copied to clippboard!", {
        position: "top-right",
        duration: 5000,
        type: "success",
        closeOnSwipe: true
      });
    }
  },

  created() {
    this.domain = process.env.VUE_APP_ROOT_DOMAIN;
  }
};
</script>
