nctions,\n  the Independent Vendor is not his subordinate and neither receives a fixed\n  remuneration that can be similar to the payment of a salary. In order to\n  celebrate this Contract, the parties state that they exercise it freely,\n  spontaneously and voluntarily, this being how the following are stipulated:\n  Clauses: FIRST Object of the contract. The purpose of this Contract is to\n  develop activities for the commercialization of virtual (digital) products\n  through the direct, network or multi-level marketing system, where the\n  Independent Vendor will use the platforms owned by the company 4view.io\n  "),_c('br'),_c('br'),_vm._v("SECOND. Rights of the Independent Vendor. The following are the Rights\n  of the Seller besides those conferred by the Law; a) Have a virtual office\n  to monitor your network; b) To receive the payment of the commission to\n  which they have a right in a timely manner. Paragraph. As long as the\n  member's account is not suspended. c) To receive all the benefits\n  contemplated in the compensation plan.\n  "),_c('br'),_c('br'),_vm._v("THIRD. Obligations and Prohibitions of the Independent Vendor. The\n  obligations of the Independent Vendor are: a) To pay the cost of the\n  purchase of the service or product in a timely manner and in the stipulated\n  manner. b) To be trained in a permanent way in the updates that arise from\n  the business. c) Permanently support those people who are part of its\n  network. Paragraph. They are called people part of the network, all those\n  people that the Independent Vendor, link at their own risk under their code,\n  as a direct client or network multilevel, in the understanding that\n  4view.io presumes the Good Faith of each of its affiliates and / or\n  affiliates and those who become Independent Vendors. d) Not to use the name\n  4view.io, improperly, mistreating, abusing, stammering, slandering and /\n  or insulting the good name of the company. Paragraph: If this situation\n  occurs, the firm 4view.io will immediately suspend the affiliate and this\n  will lose the commissions generated at the time of the suspension. To\n  achieve the activation again, after being suspended for this reason will be\n  done directly with the legal area of    the company through legal@4view.io.\n  e) The Independent Seller MUST NOT use the name of 4view.io, to market no\n  other product that does not belong to the company or its own in an improper\n  manner. f) To keep the minimum standards of respect, harmony, ethics and\n  professionalism with 4view.io, with its affiliates - Independent vendors,\n  with the other affiliates and/or Independent Vendors who do not belong to\n  its network and with all those collaborators and/or work team that is part\n  of 4view.io. g) It is the obligation of the Independent Vendor, to process\n  in due form any claim and / or controversy that may arise, through the\n  channels provided for it. (The following channels are communication\n  channels: by 4view.io online support servicer or by email at\n  contact@4view.io) and other channels of communication or contact that the\n  company has at this time or in the future in our means of communication and\n  disclosure of our business. h) Must have moral and legal capacity to hire,\n  must be a person of legal age. All under the application of the principles\n  of good personal and commercial customs. 4view.io, is not obliged to\n  respond to communications that are sent to the company in a disrespectful,\n  improper and / or foul language. i) It is the obligation of the Independent\n  Vendor to provide 4view.io with all the documentation and information\n  required timely and truthful form at the moment of affiliation so that both\n  4view.io and the Independent Seller can comply with their Commercial\n  obligations. Paragraph: 4view.io will presume of Good Faith on the\n  information, capacity, Age of Majority and documentation supplied and\n  related by the Independent Vendor, for