ent vendor. f) The Independent Seller agrees to be aware of these\n  conversions and payment methods of the company. g) To have the right to\n  charge commissions in a certain period, the Seller must be active on the\n  date of the cut. i)Not making the payment on said date will be subject to\n  these 3 considerations: 1) Your membership will remain with expired status,\n  and therefore you will stop receiving the commissions that are generated\n  within your network for the purchases of your personal and indirect guests\n  from the moment of expiration of your membership and until you make the\n  payment of the same. In this tenor, if the time comes to make the following\n  closing the member still remains with expired status, will lose the\n  commissions generated in the previous fortnight, even those that generated\n  before the expiration of their monthly payment, since they are part of the\n  period in which he should be active in order to collect them. 2)The\n  commissions generated in other periods prior to the period that you stopped\n  paying will remain frozen and you will not lose them, but you will not be\n  able to dispose of them until you activate them again.\n  "),_c('br'),_c('br'),_vm._v("NINETH. Causes and Forms of Termination. The present contract (and the\n  affiliate account it covers) will be terminated in the following ways: a) By\n  common agreement of the parties b) Unilaterally by the Independent Seller by\n  writing to 4view.io c) It will be completed unilateral form when the\n  Independent Seller presents inappropriate behaviors or actions, slander,\n  insults that cause detriment or harm to the humanity of other Independent\n  Vendors and / or against their good name and that of 4view.io and for acts\n  of unfair competition. d)For Liquidation of the company.\n  "),_c('br'),_c('br'),_vm._v("TENTH. Suspension of the Account. The account of the Independent\n  Vendor will be suspended indefinitely for the following conditions. a) For\n  proceeding irregularly and unethically against the company. d) For\n  defamation. c) For injury or physical, verbal, written aggression against\n  4view.io and / or its administrative staff and affiliates, be exercised\n  directly and/or by any means of communication and/or social network against.\n  Paragraph: And for the others that are allowed by the Law and by those\n  described within the body of this document. Affiliates whose accounts have\n  been definitively terminated will lose all economic benefits and the entire\n  affiliate network that is linked to that account.\n  "),_c('br'),_c('br'),_vm._v("ELEVENTH. Activation of the Suspended Account. To activate the account\n  of 4view.io once suspended, the Independent Seller Shall Buy a new\n  package. Paragraph 2: All packages will expire once it has generated %200 of\n  the investment amount. After that the independent seller have the option of\n  buying a new package.\n  "),_c('br'),_c('br'),_vm._v("TWELVE. ANTI WASHING BITCOINS POLICY. In support of strengthening\n  actions in the area of  Prevention and Detection of Operations with\n  Resources of Illegal Origin and based on the legal provisions, 4view.io\n  company reserves de right to prevent any commercial operation regardless of\n  the type of monetary instrument used whenever there is any suspicion related\n  to the origin of the same whether Bitcoins laundering or any other activity\n  punished by the authorities in each country. Same Anti Bitcoins Laundering\n  policy applies to all countries where 4view.io has commercial operations,\n  and may be located locally or not in that territory. 4view.io, presumes of\n  GOOD FAITH, that the Bitcoins used by our affiliates for the exercise of our\n  activities do not originate from illicit activities.\n  "),_c('br'),_c('br'),_vm._v("THIRTEENTH. PRIVACY POLICY. In strict compliance with those\n  established by International Law and the countries where we have commercial\n  operations, which refer to respect for the fundamental rights of good name\n  and protection of privacy. 4view.io will collect those \"Personal Data\"\n  that are necessary for the adequate provision of its services. Such Personal\n  Data includes, but is not limited to the following: Full name, Email, Date\n  of Birth -Etcetera. Use of Information: The Personal Data you have provided\n  us will be used for the following purposes: a) Activities related to the\n  sale and promotion of the products we sell through our different sales\n  channels, including but not limited to, the processing of applications,\n  promotional and/or marketing activities, complaints or claims, among others.\n  b) Creation and implementation of analytical and statistical processes\n  necessary for the provision of our services; c) Contact you for any topic\n  related to the Services or this Privacy Notice. 4view.io may require the\n  transfer of your Personal Data to: Those non-affiliated third party service\n  providers auxiliary or complementary to the Services, including, companies\n  of data processing and printing of cards, companies sending marketing\n  material (printed or electronic), courier companies, advertising agencies,\n  providers of tourist services (such as airlines, hotels or car rental\n  companies), custody and guard companies of information (physical or in\n  electronic media) with the purpose that these assist in the provision of\n  Services; and External agencies for the purpose of legal representation.\n  -Management of Information: It is our obligation to provide you with the\n  appropriate mechanisms in order for you to review your personal information\n  and request any of the following actions: I) Access. You may request at any\n  time to know what information you have collected. and they are conserved by\n  4view.io II) Rectification. If any of your data is inaccurate or\n  incomplete, you can request its correction. The cancellation of the data may\n  be requested at any time; If the request is appropriate, the data cannot be\n  treated in any way for subsequent deletion.\n  "),_c('br'),_c('br'),_vm._v("FOURTEENTH. PRIZES AND INCENTIVES: The Independent Vendor has the\n  right to receive prizes and incentives that the company offers at certain\n  times and according to the policies that the company determines for said\n  awards. However, regardless of the specific conditions of each Prize and\n  Incentive for each Promotion, the Independent Vendors must comply 100% with\n  the following conditions and restrictions: 1) Be active in the company,\n  according to the provisions of the Clauses. 2)To remain active in the\n  company in the following 6 months after obtaining the prize. 4)In the event\n  that the prize is a trip paid for by the company, the obligation of the\n  award-winning Independent Vendor is to remain active during the entire\n  period from the awarding of the prize to its use. 5)The case of Travel\n  Awards to countries where they apply for VISA, the process of the same is\n  the responsibility of the Independent Vendor benefited with the award. The\n  company is not responsible for this procedure. The independent seller must\n  cover the corresponding expenses of these procedures. 6)For the claim of\n  Travel Prizes Paid by the company to countries where a VISA is required, the\n  Independent Vendor must send, together with his request for the claim of his\n  prize, a photocopy of his passport where the valid VISA issued is\n  appreciated by Competent Authority on behalf of the winning Independent\n  Vendor, to enter the country of destination of the prize. 7)In the case of a\n  Travel Prize paid by the company, the Independent Vendor agrees to abide by\n  the terms and conditions imposed or in force by the company responsible for\n  delivering the prize to the Independent Vendor. These terms and conditions\n  are independent of the terms and conditions of the company. The company has\n  no interference in these terms, since they are the terms 